import axios from 'axios'
import { Net } from '../appconfig/appconfig'

// 基础配置
axios.defaults.headers['Content-Type'] = 'application/json'
axios.defaults.timeout = 10000

// 发送请求
function request (path, params, method, type = 'JSON') {
  params = params || {}
  let apiUrl = path
  if (path.indexOf('http://') === -1 && path.indexOf('https://') === -1) {
    apiUrl = Net.host + Net.apiVer + path
  }
  if (method === 'POST') {
    if (type === 'JSON') {
      return axios.post(apiUrl, params)
    } else {
      return axios({
        method: 'post',
        url: apiUrl,
        data: params,
        headers: {
          'Content-Type': 'application/xml'
        }
      })
    }
  } else {
    return axios.get(apiUrl, { params: params })
  }
}

function clearParams (params) {
  if (!params) {
    return
  }
  Object.keys(params).forEach((key) => {
    if (!params[key] && params[key] !== 0) {
      delete params[key]
    }
  })
  return params
}

export const HttpClient = {
  /**
   * get请求
   * @param {*} path
   * @param {*} params
   */
  get (path, params = null) {
    return request(path, clearParams(params), 'GET')
  },
  /**
   * post请求
   * @param {*} path
   * @param {*} params
   */
  post (path, params = null) {
    return request(path, clearParams(params), 'POST')
  },
  /**
   * postXML请求
   * @param {*} path
   * @param {*} params
   */
  postXML (path, params = null) {
    return axios.post(path, clearParams(params), 'POST', 'XML')
  },
  postForm (path, params = {}) {
    let form = new FormData()
    Object.keys(params).forEach(key => {
      form.append(key, params[key])
    })

    return axios.post(path, form, {headers: {'Content-Type': 'multipart/form-data;charset=UTF-8'}})
  },
  /**
     * 请求 方法
     */
  method: {
    post: 'POST',
    get: 'GET'
  }
}



