<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div @click="click">打开DemoPage2</div>
  </div>
</template>

<script>
import { HttpClient } from '../../core/HttpClient'
import { Router } from '../../core/router/Router'
document.title = 'demo-page1'
export default {
  name: "DemoPage1",
  data() {
    return {
      msg: "I am DemoPage1!"
    };
  },
  methods: {
    click() {
      Router.navigate('/demo/page2')
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
</style>



